import {Component, OnInit} from '@angular/core';
import {IUser} from '../iuser';
import {ActivatedRoute} from '@angular/router';
import {FormBuilder, FormControl, FormGroup, NgForm} from '@angular/forms';
import {IContact} from '../icontact';
import {environment} from '../../environments/environment';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss'],
  providers: [FormBuilder]
})
export class UserComponent implements OnInit {
  public user: IUser;
  public formGroup: FormGroup;
  public isCurrentUser: boolean;
  private isExisting: boolean;
  private nickNameControl: FormControl;
  private firstNameControl: FormControl;
  private lastNameControl: FormControl;
  private emailControl: FormControl;
  private curPassword: FormControl;
  private password: FormControl;
  private repPassword: FormControl;
  public isFirstLogIn: boolean;
  public errorText: string;

  constructor(private route: ActivatedRoute, private formBuilder: FormBuilder, private httpClient: HttpClient) {
  }

  private initControls() {
    const nickname = this.user && this.user.nickName;
    this.isExisting = Boolean(nickname);
    this.nickNameControl = new FormControl({value: nickname, disabled: this.isExisting});
    this.lastNameControl = new FormControl(this.user && this.user.lastName);
    this.firstNameControl = new FormControl(this.user && this.user.firstName);
    this.emailControl = new FormControl(this.user && this.user.email);
    this.curPassword = new FormControl();
    this.password = new FormControl();
    this.repPassword = new FormControl();
  }

  public ngOnInit(): void {
    const currentUser: IUser = JSON.parse(localStorage.getItem('user'));
    this.user = this.route.snapshot.data.userRequested;
    this.isCurrentUser = currentUser.nickName === this.user.nickName;
    this.initControls();
    this.formGroup = this.formBuilder.group({
      nickName: this.nickNameControl,
      firstName: this.firstNameControl,
      lastName: this.lastNameControl,
      email: this.emailControl,
      curPassword: this.curPassword,
      password: this.password,
      repPassword: this.repPassword
    });
    this.isFirstLogIn = (JSON.parse(localStorage.getItem('user')) as IUser).isFirstLogin;
  }

  public getUserProperties(): Array<any> {
    return Object.entries(this.user);
  }

  public isRight(right: Array<any>): boolean {
    return typeof right[1] === 'boolean';
  }

  public getHumanReadableRight(right: [string, boolean]): string {
    let result: string = '';
    let character: string = '';
    let i = 0;
    while (i < right[0].length) {
      character = right[0].charAt(i);
      if (character === character.toUpperCase()) {
        result += ' ' + character.toLowerCase();
      }
      if (character === character.toLowerCase()) {
        result += character;
      }
      i++;
    }
    return result;
  }

  submit(e: NgForm) {
    const formValue = e.value;

    if (formValue.password !== formValue.repPassword) {
      return alert('Password and password repeat should match.');
    }

    this.httpClient.put(environment.api + 'user', {
      ...formValue,
      token: localStorage.getItem('AuthToken')
    })
      .subscribe((response: any): void => {
        this.errorText = '';
        if (formValue.password) {
          localStorage.clear();
        }
        window.location.reload();
      }, (error: any) => {
        this.errorText = error.error.m;
      });
  }
}
