import {Injectable} from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanLoad,
  Route,
  Router,
  RouterStateSnapshot
} from '@angular/router';
import {Observable, ObservableInput, of} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {ISimpleResponse} from './i-simple-response';
import {catchError, pluck, tap} from 'rxjs/internal/operators';
import {environment} from '../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService implements CanActivate, CanLoad {
  canLoad(route: Route): Observable<boolean> {
    return this.resolver(route);
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.resolver(route, state);
  }

  private resolver(route: ActivatedRouteSnapshot | Route, state?: RouterStateSnapshot) {
    const token = localStorage.getItem('AuthToken');

    if (token) {
      return this.httpClient.post<ISimpleResponse>(environment.api + 'uservalid', {token})
        .pipe(
          catchError((): ObservableInput<ISimpleResponse> => {
            localStorage.clear();
            return of({success: false});
          }),
          tap((data: ISimpleResponse) => {
            localStorage.setItem('user', JSON.stringify(data.user));
          }),
          pluck<ISimpleResponse, boolean>('success')
        );
    }

    this.route.navigate(['/login']);
    return of(false);
  }

  constructor(private httpClient: HttpClient, private route: Router, private router: Router) {
  }
}
