import { Injectable } from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import {IArticle} from './iarticle';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {environment} from '../environments/environment';
import {map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ArticlesResolverService implements Resolve<Array<IArticle>> {

  constructor(private httpClient: HttpClient) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Array<IArticle>> {
    return this.httpClient.get(environment.api + 'article', {params: {token: localStorage.getItem('AuthToken')}})
      .pipe(
        map((data: any): Array<IArticle> => {
          console.log(data);
          return data.map((article: any): IArticle => ({
            url: article.url,
            updatedAt: article.updatedAt,
            logo: article.logo,
            isPublished: article.isPublished,
            isDeleted: article.isDeleted,
            header: article.header,
            entityId: article.entityId,
            deletedAt: article.isDeleted,
            createdAt: article.createdAt
          }));
        })
      );
  }
}
