import {Injectable} from '@angular/core';
import {Resolve, Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import {ISimpleResponse} from './i-simple-response';
import {catchError, filter, pluck, tap} from 'rxjs/internal/operators';
import {Observable, ObservableInput, of} from 'rxjs';
import {environment} from '../environments/environment';
import {debug} from 'util';

@Injectable({
  providedIn: 'root'
})
export class AuthResolveService implements Resolve<boolean> {
  resolve(): Observable<boolean> {
    const token = localStorage.getItem('AuthToken');

    if (token) {
      return this.httpClient.post<ISimpleResponse>(environment.api + 'uservalid', {token})
        .pipe(
          catchError((): ObservableInput<ISimpleResponse> => {
            localStorage.clear();
            return of({success: false});
          }),
          tap((data: ISimpleResponse) => {
            if (data.success) {
              localStorage.setItem('user', JSON.stringify(data.user));
            }
          }),
          pluck<ISimpleResponse, boolean>('success'),
        );
    }

    return of(false);
  }

  constructor(private httpClient: HttpClient, private router: Router) {
  }
}
