import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IImage} from "../about/about.component";
import {TImageStatus} from "../types";

@Component({
  selector: 'app-image-upload',
  templateUrl: './image-upload.component.html',
  styleUrls: ['./image-upload.component.scss']
})
export class ImageUploadComponent implements OnInit {
  @Output() public changeMedia: EventEmitter<Array<IImage>> = new EventEmitter<Array<IImage>>();
  @Input() public init: Array<IImage>;

  public mediaObjects: Array<IImage> = [];
  private filesToUpload: Array<File>;

  handleFileInput(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;
    const mediaToPushQ: Array<Promise<IImage>> = Object.values(this.filesToUpload).map((file: File): Promise<IImage> =>
      new Promise((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = (e: any) => {
          resolve({
            src: e.target.result,
            alt: 'File to upload',
            status: 'to upload',
            file
          });
        };
        reader.onerror = (e: any) => {
          reject(e);
        };
        reader.readAsDataURL(file);
      })
    );

    Promise.all(mediaToPushQ)
      .then((mediaToPush: Array<IImage>) => {
        this.mediaObjects.push(...mediaToPush);
        this.changeMedia.emit(this.mediaObjects);
      });
  }

  setImageStatus(status: TImageStatus, img: IImage) {
    img.oldStatus = img.status;
    img.status = status;

    this.changeMedia.emit(this.mediaObjects);
  }

  ngOnInit() {
    this.mediaObjects = [...this.init];
  }
}
