import { Injectable } from '@angular/core';
import {IFileUploadResponse} from "../i-file-upload-response";
import {environment} from "../../environments/environment";
import {Observable, Observer} from "rxjs/index";
import {HttpClient} from "@angular/common/http";

@Injectable({
  providedIn: 'root'
})
export class ImageUploadService {

  constructor(private httpClient: HttpClient) { }

  public uploadMedia(filesToUpload: Array<File>): Observable<Array<IFileUploadResponse>> {
    if (filesToUpload && filesToUpload.length) {
      const formData: any = new FormData();
      const files: Array<File> = filesToUpload;

      for (let i = 0; i < files.length; i++) {
        formData.append('upload', files[i], files[i].name);
      }

      return this.httpClient.post<Array<IFileUploadResponse>>(environment.api + 'upload', formData);
    }

    return Observable.create((observer: Observer<Array<null>>): void => {
      observer.next([]);
    });
  }
}
