import { Component, OnInit } from '@angular/core';
import {IPageLanguageContainer} from '../ipage-language-container';
import {ActivatedRoute} from '@angular/router';
import {IReview} from "../reviews-resolver.service";

@Component({
  selector: 'app-reviews',
  templateUrl: './reviews.component.html',
  styleUrls: ['./reviews.component.scss']
})
export class ReviewsComponent implements OnInit {
  public langObject: IPageLanguageContainer;
  public reviews: Array<IReview>;

  constructor(private route: ActivatedRoute) { }

  deleteReview(review: IReview) {
    const index = this.reviews.findIndex((element: IReview): boolean => element._id === review._id);
    this.reviews.splice(index, 1);
  }

  ngOnInit() {
    this.langObject = this.route.snapshot.data.pageData;
    this.reviews = this.route.snapshot.data.pageData;
  }
}
