import {Component, OnInit} from '@angular/core';
import {HttpClient, HttpEvent} from '@angular/common/http';
import {ISimpleResponse} from '../i-simple-response';
import {ActivatedRoute, Router} from '@angular/router';
import {environment} from '../../environments/environment';
import {IUser} from '../iuser';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public login: string;
  public password: string;
  public errorText: string;
  private redirectUrl: string = '';

  constructor(private httpClient: HttpClient, private router: Router, private route: ActivatedRoute) {
  }

  submit(f) {
    this.httpClient.post<ISimpleResponse>(environment.api + 'login', f.value)
      .subscribe((response: ISimpleResponse) => {
        if (response.success) {
          localStorage.setItem('AuthToken', response.message);
          localStorage.setItem('user', JSON.stringify(response.user));

          const isFirstLogin = (JSON.parse(localStorage.getItem('user')) as IUser).isFirstLogin;
          const nickName = (JSON.parse(localStorage.getItem('user')) as IUser).nickName;

          if (isFirstLogin) {
            this.redirectUrl = `/user/${nickName}`;
          }
          this.errorText = '';
          this.router.navigate([this.redirectUrl]);
        } else {
          alert(response.message);
          this.errorText = response.message;
        }
      }, error => {
        this.errorText = error.error.message;
      });
  }

  ngOnInit() {
    if ((JSON.parse(localStorage.getItem('user')) as IUser)) {
      const isFirstLogin = (JSON.parse(localStorage.getItem('user')) as IUser).isFirstLogin;
      const nickName = (JSON.parse(localStorage.getItem('user')) as IUser).nickName;

      if (isFirstLogin) {
        this.redirectUrl = `/user/${nickName}`;
      }
    }

    const loginSuccess = this.route.snapshot.data.loginSuccess;

    if (loginSuccess) {
      this.router.navigate([this.redirectUrl]);
    }
  }
}
