import {Component, DoCheck, OnChanges, OnInit} from '@angular/core';
import {INavigationLink} from './_index/index.component';
import {NavigationEnd, Router, RouterEvent} from '@angular/router';
import {filter, map, tap} from 'rxjs/internal/operators';
import {Observable} from 'rxjs';
import {IUser} from './iuser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  public nav: Array<INavigationLink> = [
    {name: 'About', href: 'about'},
    {name: 'Articles', href: 'articles'},
    {name: 'Diplomas', href: 'diplomas'},
    {name: 'Services', href: 'services'},
    {name: 'Reviews', href: 'reviews'},
    {name: 'Contacts', href: 'contacts'},
  ];
  public userName: string;
  public isFirstLogin: boolean;

  constructor(private router: Router) {
  }

  ngOnInit() {
    this.setFirstLogin();
    this.router.events.pipe(
      filter(() => JSON.parse(localStorage.getItem('user'))),
      tap(() => {
        this.userName = (JSON.parse(localStorage.getItem('user')) as IUser).nickName;
      }),
      filter<RouterEvent>((e: RouterEvent) => e instanceof NavigationEnd),
      map((e: NavigationEnd) => e)
    ).subscribe(this.setFirstLogin.bind(this));
  }

  private setFirstLogin() {
    if (JSON.parse(localStorage.getItem('user'))) {
      this.userName = (JSON.parse(localStorage.getItem('user')) as IUser).nickName;
      this.isFirstLogin = (JSON.parse(localStorage.getItem('user')) as IUser).isFirstLogin;
    }

    if (this.isFirstLogin) {
      this.router.navigate([`/user/${this.userName}`]);
    }
  }

  logout() {
    localStorage.clear();
    window.location.reload();
  }
}
