import {Component, OnInit} from '@angular/core';
import {IPageLanguageContainer} from '../ipage-language-container';
import {ActivatedRoute} from '@angular/router';
import {NgForm} from '@angular/forms';
import {languages} from '../const';
import {IPage} from '../ipage';
import {environment} from '../../environments/environment';
import {IPageSubmit} from '../ipage-submit';
import {HttpClient} from '@angular/common/http';
import {IContact} from '../icontact';
import {TLanguage} from '../types';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.scss']
})
export class ContactsComponent implements OnInit {
  private langObject: IPageLanguageContainer;
  public model: any;
  public lang: string;
  private requestObj: IPageSubmit = {
    id: 'contacts',
    page: {},
    media: []
  };

  constructor(private route: ActivatedRoute, private httpClient: HttpClient) {
  }

  ngOnInit() {
    this.langObject = this.route.snapshot.data.pageData;
    this.model = this.langObject.en.pageData;
  }

  submit(e: NgForm) {
    const result = {};
    for (const lang in this.langObject) {
      if (this.langObject[lang]) {
        result[lang] = {pageData: this.langObject[lang].pageData.map((oldContact: IContact) => ({
          ...oldContact,
          account: this.model.find((newContact: IContact) => newContact.type === oldContact.type).account
        }))};
      }
    }

    this.requestObj.page = result;

    this.httpClient.put(environment.api + 'interface', {
      ...this.requestObj,
      token: localStorage.getItem('AuthToken')
    })
      .subscribe((response: any): void => {
        window.location.reload();
      });
  }
}
