import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../environments/environment';
import {IPageSubmit} from '../ipage-submit';
import {NgForm} from '@angular/forms';
import {languages} from '../const';
import {IPage} from '../ipage';

export interface INavigationLink {
  name: string;
  href: string;
}

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  public lang = 'en';
  public model: any;
  private requestObj: IPageSubmit = {
    id: 'main',
    page: {},
    media: []
  };
  public notificationEmail: string;

  constructor(private route: ActivatedRoute, private httpClient: HttpClient) {
  }

  ngOnInit() {
    this.model = this.route.snapshot.data.pageData;
    this.notificationEmail = this.model.en.pageData.notificationEmail;
  }

  submit(e: NgForm) {
    for (const index in languages) {
      if (languages.hasOwnProperty(index)) {
        const language = languages[index];
        this.requestObj.page[language] = {pageData: e.value[language]} as IPage;
        this.requestObj.page[language].pageData.email = this.notificationEmail;
      }
    }

    this.httpClient.put(environment.api + 'interface', {
      ...this.requestObj,
      token: localStorage.getItem('AuthToken')
    })
      .subscribe((response: any): void => {
        window.location.reload();
      });
  }
}
