import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {IPageLanguageContainer} from '../ipage-language-container';
import {IPageImage, IPageSubmit} from '../ipage-submit';
import {NgForm} from '@angular/forms';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../environments/environment';
import {switchMap} from 'rxjs/internal/operators';
import {IFileUploadResponse} from '../i-file-upload-response';
import {Observable, Observer} from 'rxjs';
import {ImageUploadService} from '../image-upload/image-upload.service';
import {TImageStatus} from '../types';


export interface IImage {
  src: string;
  alt: string;
  status: TImageStatus;
  oldStatus?: TImageStatus;
  file?: File;
}

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  public langObject: IPageLanguageContainer;
  public lang = 'en';
  private requestObj: IPageSubmit = {
    id: 'about',
    page: {},
    media: []
  };
  public pageMedia: Array<IImage> = [];
  private mediaObjects: Array<IImage> = [];

  constructor(private route: ActivatedRoute, private httpClient: HttpClient, private imageUpload: ImageUploadService) {
  }

  public submit(e: NgForm) {
    const filedToUpload: Array<IImage> = this.mediaObjects
      .filter((media: IImage): boolean => media.status === 'to upload');

    const filesToRemove: Array<IImage> = this.mediaObjects
      .filter((media: IImage): boolean => (media.status === 'removed' && media.oldStatus !== 'to upload'));

    this.imageUpload.uploadMedia(filedToUpload.map((media: IImage): File => media.file))
      .pipe(
        switchMap((files: Array<IFileUploadResponse>): Observable<any> => {
          this.requestObj.media = files.map((file: IFileUploadResponse): IPageImage => ({
            src: file.url,
            status: 'add'
          }));

          this.requestObj.media.push(...filesToRemove.map((file: IImage): IPageImage => ({
              src: file.src,
              status: 'remove'
          })));

          for (const i in e.controls) {
            if (!e.controls.hasOwnProperty(i)) {
              continue;
            }

            if (e.controls[i].dirty) {
              this.requestObj.page[i] = e.controls[i].value;
            }
          }

          return this.httpClient.put(environment.api + 'interface', {
            ...this.requestObj,
            token: localStorage.getItem('AuthToken')
          });
        })
      )
      .subscribe((files: any): void => {
        window.location.reload();
      });
  }

  public ngOnInit() {
    this.langObject = this.route.snapshot.data.pageData;
    this.pageMedia = this.route.snapshot.data.pageData.en.images.map((img: string): IImage => ({
      src: img,
      alt: '',
      status: 'in database'
    }));
  }

  public setPageMedia($event: Array<IImage>) {
    this.mediaObjects = $event;

    const inDatabase: Array<IImage> = this.mediaObjects.filter((media: IImage): boolean => media.status === 'in database');
    const toUpload: Array<IImage> = this.mediaObjects.filter((media: IImage): boolean => media.status === 'to upload');
    const removed: Array<IImage> = this.mediaObjects.filter((media: IImage): boolean => media.status === 'removed');

    console.log('----------------------------');
    console.log('in database', inDatabase);
    console.log('to upload', toUpload);
    console.log('removed', removed);
  }
}
