import { Injectable } from '@angular/core';
import {ActivatedRouteSnapshot, Resolve} from "@angular/router";
import {map} from "rxjs/internal/operators";
import {IPage} from "./ipage";
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs/index";
import {environment} from "../environments/environment";
import {TLanguage} from "./types";

export interface IReview {
  name?: string;
  review: string;
  email?: string;
  isPublished?: boolean;
  language: TLanguage;
  _id: string;
}

@Injectable({
  providedIn: 'root'
})
export class ReviewsResolverService implements Resolve<Array<IReview>> {
  resolve(route: ActivatedRouteSnapshot): Observable<Array<IReview>> {
    return this.httpClient.get<Array<IReview>>(environment.api + '/review', {
      params: {token: localStorage.getItem('AuthToken')}
    });
  }

  constructor(private httpClient: HttpClient) {
  }
}
