import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {IReview} from "../reviews-resolver.service";
import {HttpClient} from "@angular/common/http";
import {environment} from "../../environments/environment";

@Component({
  selector: 'app-review',
  templateUrl: './review.component.html',
  styleUrls: ['./review.component.scss']
})
export class ReviewComponent implements OnInit {
  @Input() review: IReview;
  @Output() deleteReview: EventEmitter<IReview> = new EventEmitter<IReview>();

  constructor(private httpClient: HttpClient) {
  }

  ngOnInit() {
  }

  setPublishFlag(review: IReview) {
    this.httpClient.put(environment.api + 'review', {
      id: review._id,
      isPublished: !review.isPublished,
      token: localStorage.getItem('AuthToken')
    })
      .subscribe((newReview: IReview) => {
        review.isPublished = !review.isPublished;
      })
  }

  removeReview(review: IReview) {
    this.httpClient.delete(environment.api + 'review', {params: {
        id: review._id,
        token: localStorage.getItem('AuthToken')
      }
    })
      .subscribe(() => {
        this.deleteReview.emit(review);
      })
  }
}
